import React from 'react';
import CardEvent from '../components/CardEvent';

import uptapImg from '../../../assets/images/uptap.png';
import { androidImg, arduinoImg, javaImg, htmlImg, bootstrapImg } from '../../Icons';

const Universidad = () => {
  return (
    <CardEvent
      date="2012 - 2016"
      logo={{
        src: uptapImg,
        alt: 'Universidad Politécnica de Tapachula',
      }}
      header={<h2>Universidad Politécnica de Tapachula</h2>}
      body={
        <div>
          <p className="pl-2 ">
            Estudios especializados en el diseño, desarrollo e implementación de software
          </p>
        </div>
      }
      footer={
        <div className="py-1 flex justify-around">
          <img src={javaImg} alt="" className="w-8 h-8" />
          <img src={htmlImg} alt="" className="w-8 h-8" />
          <img src={bootstrapImg} alt="" className="w-8 h-8" />
          <img src={arduinoImg} alt="" className="w-8 h-8" />
          <img src={androidImg} alt="" className="w-8 h-8" />
        </div>
      }
    />
  );
};

export default Universidad;
