import React from 'react';
import CardEvent from '../../components/CardEvent';

import uptapImg from '../../../../assets/images/ennova.png';

import {
  javaImg,
  javascriptImg,
  cssImg,
  bootstrapImg,
  htmlImg,
  mysqlImg,
  springImg,
} from '../../../Icons';

const EnnovasoftInicio = () => {
  return (
    <CardEvent
      date="16/02/2018"
      header={<h2>Ennovasoft sa de cv</h2>}
      logo={{
        src: uptapImg,
        alt: 'Ennovasoft sa de cv',
      }}
      body={
        <div>
          <div className="flex justify-between">
            <p className="pr-2 order-2 md:order-1">
              Inicio en el puesto de Desarrollador Full stack, mas enfocado en desarrollo web,
              en mis roles esta la creación de paginas estáticas y aplicaciones web
            </p>
          </div>
        </div>
      }
      footer={
        <div className="py-1 flex justify-around">
          <img src={javascriptImg} alt="" className="w-8 h-8" />
          <img src={javaImg} alt="" className="w-8 h-8" />
          <img src={mysqlImg} alt="" className="w-8 h-8" />
          <img src={cssImg} alt="" className="w-8 h-8" />
          <img src={htmlImg} alt="" className="w-8 h-8" />
          <img src={springImg} alt="" className="w-8 h-8" />
          <img src={bootstrapImg} alt="" className="w-8 h-8" />
        </div>
      }
    />
  );
};

export default EnnovasoftInicio;
