import './styles.scss';
import React from 'react';
import PropTypes from 'prop-types';

import { TextEvent } from '@merc/react-timeline';

const Content = ({ children }) => <div className="card__event__container">{children}</div>;

const CardEvent = ({ date, header = null, body = null, footer = null, logo = null }) => {
  return (
    <TextEvent date={date} text="" card={Content}>
      <div className="card__event__header">
        {logo && <img src={logo.src} alt={logo.alt} className="card__event__logo" />}

        {header && <div className="card__event__title">{header}</div>}
      </div>

      {body && <div className="card__event__body">{body}</div>}

      {footer && <div className="card__event__footer">{footer}</div>}
    </TextEvent>
  );
};

CardEvent.propTypes = {
  date: PropTypes.string.isRequired,
  header: PropTypes.string || PropTypes.node,
  body: PropTypes.string || PropTypes.node,
  footer: PropTypes.string || PropTypes.node,
  logo: {
    src: PropTypes.string.isRequired,
    alt: PropTypes.string.isRequired,
  },
};
export default CardEvent;
