import React from 'react';
import CardEvent from '../../components/CardEvent';

import uptapImg from '../../../../assets/images/ennova.png';

const EnnovasoftTermino = () => {
  return (
    <CardEvent
      date="26/03/2020"
      header={<h2>Ennovasoft sa de cv</h2>}
      logo={{
        src: uptapImg,
        alt: 'Ennovasoft sa de cv',
      }}
      body={
        <div>
          <div className="flex justify-between">
            <p className="pr-2 order-2 md:order-1">
              Culmina mi segundo contrato, y decido no renovar para seguir nuevas metas.
            </p>
          </div>
        </div>
      }
    />
  );
};

export default EnnovasoftTermino;
