import React from 'react';
import CardEvent from '../../components/CardEvent';

import { phpImg, javascriptImg, cssImg, htmlImg, bootstrapImg, vueImg } from '../../../Icons';

const Laboratorio = () => {
  return (
    <CardEvent
      date="15/06/2018 - 10/09/2018"
      header={<h2>Laboratorio de análisis clínicos - V1</h2>}
      body={
        <div>
          <p className="pl-2 ">
            Desarrollo de sistema para manejo de pacientes y resultados de análisis clínicos
          </p>
        </div>
      }
      footer={
        <div className="py-1 flex justify-around">
          <img src={phpImg} alt="" className="w-8 h-8 object-scale-down" />
          <img src={javascriptImg} alt="" className="w-8 h-8 object-scale-down" />
          <img src={vueImg} alt="" className="w-8 h-8 object-scale-down" />
          <img src={htmlImg} alt="" className="w-8 h-8 object-scale-down" />
          <img src={cssImg} alt="" className="w-8 h-8 object-scale-down" />
          <img src={bootstrapImg} alt="" className="w-8 h-8 object-scale-down" />
        </div>
      }
    />
  );
};

export default Laboratorio;
