import React from 'react';
import { Timeline as TimeLineReact, Events, createTheme, themes } from '@merc/react-timeline';

import { useDarkTheme } from '../../context/DarkTheme';

// import Preparatoria from './Events/Preparatoria';
import Universidad from './Events/Universidad';
import EnnovasoftInicio from './Events/Ennovasoft/Inicio';
import EnnovasoftTermino from './Events/Ennovasoft/Termino';

import Laboratorio1 from './Events/Laboratorio/Laboratorio1';
import Laboratorio2 from './Events/Laboratorio/Laboratorio2';

const TimeLine = () => {
  const { darkActive } = useDarkTheme();
  const greenColor = darkActive ? 'rgba(30, 58, 138,1)' : 'rgba(249, 115, 22, 1)';

  const theme = createTheme(themes.default, {
    timelineTrack: {
      backgroundColor: greenColor,
    },

    date: {
      backgroundColor: greenColor,
    },

    marker: {
      borderColor: greenColor,
    },
  });

  return (
    <div className="size__container">
      <TimeLineReact theme={theme}>
        <Events>
          <Universidad />

          <EnnovasoftInicio />
          <Laboratorio1 />
          <Laboratorio2 />

          <EnnovasoftTermino />
        </Events>
      </TimeLineReact>
    </div>
  );
};

export default TimeLine;
